<template>
<div>
  <el-row class="tac">
  <el-col :span="4">
    <el-input
      placeholder="输入关键字进行搜索"
      v-model="filterText">
    </el-input>
    <el-tree
      class="filter-tree"
      :data="notes"
      :props="defaultProps"
      default-expand-all
      :filter-node-method="filterNode"
      ref="tree"
      @node-click="handleNodeClick">
    </el-tree>
  </el-col>
  <el-col :span="19" style="margin: 20px;text-align: left">
    <h3>{{note.title}}</h3><h5>{{note.date}}</h5>
    <p style="text-indent: 2em" v-html="note.text"></p>
  </el-col>
  </el-row>
</div>
</template>
<script>
  export default {
    watch: {
      filterText(val) {
        this.$refs.tree.filter(val);

      }
    },
    data () {
      return {
        notes: [],
        note:{},
        defaultProps: {
          label:'title'
        },
        filterText:''
      }
    },
    created(){
      this.$ajax.get('/note/getAllNotes').then((res) => {
        this.notes=res.data;
      }).catch(res => {
        console.log(res)
      });
    },
    methods: {
      handleNodeClick(data) {
        console.log(data.id);
        this.note = data;
      },
      filterNode(value, data) {
        if (!value) return true;
        return data.title.indexOf(value) !== -1;
      }
    }
  }
</script>
<style>
  .note_title{
    text-align: left;
  }
</style>



